}

.logo__photo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 20px;
}

.header__menu {
  z-index: 5;
  opacity: 1;
  transition: opacity 250ms var(--cubic);
  background-color: rgba(0, 0, 0, 0);
  margin-left: auto;
  position: relative;
}

.is-open {
  opacity: 0;
}

.header__dutton-end {
  z-index: 1001;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 23px;
  right: 28px;
}

.header__button {
  color: var(--accent-text);
  border-radius: 22px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3125;
}

.header__button svg {
  margin-left: 24px;
}

.header__button:hover, .header__button:focus {
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.header__button-two {
  width: 100%;
  height: 44px;
  background-color: #fff;
  margin-bottom: 32px;
}

.nav-list {
  gap: 22px;
  display: grid;
}

.nav-list__link {
  z-index: 5;
  color: #fff;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.28571;
  transition: color .25s linear;
  position: relative;
}

.nav-list__link:hover, .nav-list__link:focus, .nav-list__link--color {
  color: var(--accent-text);
}

.nav-list__link-decoration-r {
  color: var(--accent-text);
  opacity: 0;
  transition: transform .25s linear, opacity .25s linear;
  display: block;
  position: absolute;
  top: 0;
  right: -10px;
  transform: translate(25px);
}

.nav-list__link:hover .nav-list__link-decoration-r, .nav-list__link:focus .nav-list__link-decoration-r {
  opacity: 1;
  transform: translate(0);
}

.nav-list__link-decoration-l {
  color: var(--accent-text);
  opacity: 0;
  transition: transform .25s linear, opacity .25s linear;
  display: block;
  position: absolute;
  top: 0;
  left: -10px;
  transform: translate(-25px);
}

.nav-list__link:hover .nav-list__link-decoration-l, .nav-list__link:focus .nav-list__link-decoration-l {
  opacity: 1;
  transform: translate(0);
}

