  .nav-box {
    padding: 60px 21px;
  }

  .nav {
    z-index: 1000;
    width: 250px;
    height: 100%;
    background-color: var(--accent);
    opacity: 0;
    transition: transform .25s linear, opacity .25s linear;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    transform: translate(100%);
  }

  .nav--show {
    opacity: 1;
    transform: translate(0);
  }
}

